}\n\n.auth--profileModal--title {\n  @include medium_kor;\n  margin-top: 5px;\n  margin-left: $image_margin;\n}\n\n.auth--profileModal--xmark {\n  width: 15px;\n  position: absolute;\n  right: 15px;\n  top: 15px;\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n}\n\n.auth--profileModal--profileWrapper {\n  @include desktop {\n    width: 480px;\n  }\n  @include mobile {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(25%, auto));\n    justify-items: center;\n  }\n  @include galaxyFold {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(calc(100% / 3), auto));\n    justify-items: center;\n  }\n  margin-top: 20px;\n}\n\n.auth--profileModal--profileImg {\n  width: 60px;\n  margin: $image_margin;\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n}\n\n.auth--profileModal--cancleWrapper {\n  display: flex;\n  justify-content: end;\n  margin-bottom: 10px;\n}\n\n.auth--profileModal--cancle {\n  @include medium_kor;\n  color: $lightblue;\n  text-align: end;\n  margin-right: $image_margin;\n  &:hover {\n    cursor: pointer;\n    transform: scale(1.1);\n  }\n}\n","@import '@css/fonts.scss';\n\n$basic_width: 700px;\n$desktop: 1024px;\n\n// device mode\n@mixin desktop {\n  @media (min-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin mobile {\n  @media (max-width: $basic_width) {\n    @content;\n  }\n}\n\n@mixin galaxyFold {\n  @media (max-width: 300px) {\n    @content;\n  }\n}\n\n// utils\n@mixin basic--wrappers {\n  width: $basic_width;\n  margin: 0 auto;\n}\n","// family\n$kor_font_regular: 'Noto Sans KR', sans-serif;\n$eng_font_regular: 'Roboto', sans-serif;\n\n// color\n$darkgrey_darker: #252323;\n$darkgrey_default: rgb(80, 80, 80);\n$darkgrey_medium: rgb(140, 140, 140);\n$darkgrey_light: rgb(184, 184, 184);\n$white: white;\n$lightblue: #73b2ed;\n$pinkred: #ec7063;\n$red_for_submit: #9f062ba3;\n\n//size\n$main_title_22: 22px;\n$sub_title_18: 18px;\n$default_medium_15: 15px;\n$default_small_13: 13px;\n\n// font-set-default\n@mixin medium_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_kor {\n  font-family: $kor_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n// font-set-white\n@mixin big_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $sub_title_18;\n  color: $white;\n}\n\n@mixin medium_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $white;\n}\n"],"sourceRoot":""}]);
